.AboutCard-Container {
  display: flex;
  flex-direction: column;
  justify-content: center;

  margin-top: 3rem;

  width: 100%;
  height: 100vh;
  min-height: 30rem;
  /* height: 39rem; */

  background: linear-gradient(90deg, rgb(0, 0, 0), #500303);
  background-repeat: no-repeat;

  position: relative;
}
.bg-clevelandImage {
  width: 100%;
  height: 100%;
  margin: auto;
  position: absolute;
  background-image: url("https://upload.wikimedia.org/wikipedia/tr/0/09/Cleveland-Cavaliers-Logo.png");
  background-size: 25rem;
  background-repeat: no-repeat;
  background-position: center;
  opacity: 0.03;
  z-index: 3;
  pointer-events: none;
}

.AboutCard-nm {
  margin: auto;
  color: whitesmoke;
  font-size: 2rem;
  box-shadow: black;
}
.AboutMe-Container {
  width: 100%;
  height: 75%;
  min-height: 25rem;
  margin: auto;
  margin-top: -0.3rem;

  text-align: center;

  background: linear-gradient(180deg, #800404, rgb(0, 0, 0));

  border-top: 5px solid #cea02a;

  z-index: 2;
}
.AboutMe-P {
  word-spacing: 4px;
  font-size: 1rem;
  margin: auto;
  padding: 15px 3px;
  color: rgb(214, 211, 211);
  text-shadow: 3px 3px 15px black;

  transition: text-shadow 0.3s;
  z-index: 5;
}
.AboutMe-P:hover {
  text-shadow: 3px 3px 5px black;
}

.schoolInfo {
  display: block;
  width: 80%;
  margin: auto;
  text-align: center;
  margin-top: 8vh;
}
.githubLink {
  cursor: pointer;
  color: #cea02a;
  text-decoration: none;
  text-decoration-color: rgb(104, 0, 0);

  transition: color 0.3s ease;
}
.githubLink:hover {
  color: #bd87009c;
}

.soonDiv {
  position: relative;
}
.soonTag {
  font-size: 0.7rem;
  position: absolute;
  top: -4px;
  right: 0.3rem;
  display: inline;
  color: rgba(209, 209, 209, 0.825);
  background-color: rgba(0, 0, 0, 0.516);
  z-index: 5;
}
