@media screen and (max-width: 1100px) {
  .GithubRepo {
    width: calc(92vw) !important;
    height: 9rem !important;
    border: 0px solid black !important;
    border-radius: 8px !important;
    box-shadow: 0px 0px 50px black;
  }
}

.GitHub-Container {
  display: flex;
  flex-direction: column;
  justify-content: center;

  margin-top: 3rem;

  width: 100%;
  height: 100vh;
  min-height: 30rem;

  background: linear-gradient(90deg, rgb(0, 0, 0), #155502);
  background-repeat: no-repeat;

  position: relative;
}

.Github-nm {
  margin: auto;
  color: whitesmoke;
  font-size: 2rem;
  box-shadow: black;
}

.GithubRepos-Container {
  width: 100%;
  height: 75%;
  min-height: 25rem;

  overflow-y: scroll;

  margin: auto;
  margin-top: -0.3rem;

  text-align: center;

  background: linear-gradient(180deg, #2eaf07, rgb(0, 0, 0));

  border-top: 5px solid gray;

  z-index: 2;
}

.GithubReposList {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;

  justify-content: center;
  align-items: center;

  margin: auto;

  list-style-type: none;

  z-index: 4;
}

.GithubRepoListItem {
  display: inline;
  margin: 1rem 1.2rem;
  background-color: wheat;
  border-radius: 35px;

  z-index: 4;

  transition: margin-bottom 0.1s ease, box-shadow 0.1s ease;
}

.GithubRepoListItem:hover {
  box-shadow: 0px 0px 50px rgba(5, 71, 8, 1);
  z-index: 1;
}

.GithubRepo {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 45vw;
  height: 10rem;

  border-radius: 45px;

  background: linear-gradient(
    35deg,
    #000000 0% 0%,
    #083100 20% 20%,
    #0f5501 35% 35%,
    #157a01 60% 60%,
    #1ba100 80% 80%,
    #23cf00 100% 100%
  );
  color: white;
  z-index: 4;

  transition: box-shadow 1s ease;
}

.ct-des-name {
  margin: auto auto;

  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
}
.repoName {
  margin: 0.9rem auto;
}

.repoName a {
  font-size: 1.2rem;
  text-decoration: none;
  color: white;
}
.repoName i {
  font-size: 1.2rem;
  color: rgba(0, 0, 0, 0.74);
}

.repoDescription {
  font-size: 0.8rem;
}
.repoDescription p {
  color: rgb(206, 206, 206);
}
.ct-langs {
  font-size: 0.8rem;

  margin: auto;
}
.langs-list {
  display: flex;
  flex-direction: row;
  list-style-type: none;
}
.langs-list-item {
  color: rgb(165, 157, 140);
  margin: auto 0.5rem;
}
