@import url(https://fonts.googleapis.com/css2?family=Bitter:wght@700&display=swap);
* {
  font-family: "Bitter", sans-serif;
  box-sizing: border-box;
  margin: 0 0;
  padding: 0 0;
}

span,
h1,
p {
  cursor: default;
}

input,
textarea,
button,
select,
a {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

html {
  scroll-behavior: smooth;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body::-webkit-scrollbar {
  width: 1rem;
  border-top: none;
  border-bottom: none;
  border-right: none;
}

@media screen and (max-width: 720px) {
  body::-webkit-scrollbar {
    width: 0rem;
  }
}

body::-webkit-scrollbar-track {
  border-top: none;
  border-bottom: none;
  z-index: 5;
}

body::-webkit-scrollbar-thumb {
  padding: 5px;
  z-index: 0;
  background: linear-gradient(180deg, #6d0000, #000000);
  outline: 5px solid black;
  border-radius: 100px;
}


.landingContainer {
  display: flex;
  position: relative;

  flex-wrap: wrap;

  margin: auto;
  width: 100%;
  height: 100%;
}

@media screen and (max-width: 800px) {
  .ProfileCart {
    height: calc(80vh);
  }
  .navList {
    padding: 1.2rem 0.2rem !important;
  }
  .navList-item a {
    padding: 0.8rem 1.6rem !important;
    margin: 0 1.2vw !important;
  }
}

.ProfileCart {
  margin: auto;
  display: flex;

  flex-direction: column;
  justify-content: center;
  align-items: center;

  width: 100%;
  height: calc(100vh);

  background: linear-gradient(45deg, rgb(0, 0, 0), rgb(61, 5, 165));
  background-repeat: no-repeat;

  -webkit-animation: navBarColor 10s infinite alternate;

  animation: navBarColor 10s infinite alternate;

  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}

.navImage {
  display: flex;
  justify-content: center;
  align-items: center;

  margin: 5px;
  margin-top: -5rem;
  width: 12rem;
  height: 12rem;

  background-size: 250px;
  background-position: center;

  overflow: hidden;
  border-radius: 100%;
  border: 3px solid rgb(0, 0, 0);

  background-color: gray;
  -webkit-animation: rollAnim 12s infinite;
  animation: rollAnim 12s infinite;
  transition: width 0.3s ease, height 0.3s ease, background-size 0.3s ease,
    border-width 0.3s ease, border-radius 0.3s ease;
}
.navImage:hover {
  background-size: 260px;
}

.navName {
  margin-top: 1rem;
  margin-bottom: 0.8rem;
  font-size: 2rem;

  letter-spacing: 1px;
  text-align: center;
  color: white;
}
.navList {
  list-style-type: none;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: white;
  padding: 1rem 1rem;

  border-radius: 15px;
}

.navList-item a {
  color: black;
  text-decoration: none;
  cursor: pointer;
  letter-spacing: 1px;
  font-size: 0.8rem;
  text-align: center;

  padding: 0.8rem 1.6rem;
  margin: 0 1.8vw;

  background-color: white;
  box-shadow: 0px 0px 12px rgba(61, 5, 165, 0.671);

  border-left: 8px solid rgb(61, 5, 165);
  border-right: 8px solid rgb(61, 5, 165);

  transition: border-radius 0.3s, box-shadow 0.2s ease;
}

.navList-item:nth-child(1) a {
  border-radius: 50px;
  border-top-left-radius: 0px;
  border-bottom-right-radius: 0px;
}

.navList-item:nth-child(2) a {
  border-top-left-radius: 0px;
  border-bottom-right-radius: 0px;

  border-radius: 18px;

  border-color: black;
}

.navList-item:nth-child(3) a {
  border-radius: 50px;
  border-bottom-left-radius: 0px;
  border-top-right-radius: 0px;
}
.navList-item a:hover {
  border-radius: 18px;

  border-color: black;
  box-shadow: 0px 0px 18px black;
}

@-webkit-keyframes rollAnim {
  0% {
    box-shadow: 0px 0px 150px rgb(0, 0, 0);
    border-color: rgb(255, 255, 255);
  }
  50% {
    box-shadow: 0px 0px 15px;
  }
  100% {
    border-color: rgb(255, 255, 255);
    box-shadow: 0px 0px 150px rgb(0, 0, 0);
  }
}

@keyframes rollAnim {
  0% {
    box-shadow: 0px 0px 150px rgb(0, 0, 0);
    border-color: rgb(255, 255, 255);
  }
  50% {
    box-shadow: 0px 0px 15px;
    border-color: black;
  }
  100% {
    border-color: rgb(255, 255, 255);
    box-shadow: 0px 0px 150px rgb(0, 0, 0);
  }
}

@media screen and (max-width: 1100px) {
  .GithubRepo {
    width: calc(92vw) !important;
    height: 9rem !important;
    border: 0px solid black !important;
    border-radius: 8px !important;
    box-shadow: 0px 0px 50px black;
  }
}

.GitHub-Container {
  display: flex;
  flex-direction: column;
  justify-content: center;

  margin-top: 3rem;

  width: 100%;
  height: 100vh;
  min-height: 30rem;

  background: linear-gradient(90deg, rgb(0, 0, 0), #155502);
  background-repeat: no-repeat;

  position: relative;
}

.Github-nm {
  margin: auto;
  color: whitesmoke;
  font-size: 2rem;
  box-shadow: black;
}

.GithubRepos-Container {
  width: 100%;
  height: 75%;
  min-height: 25rem;

  overflow-y: scroll;

  margin: auto;
  margin-top: -0.3rem;

  text-align: center;

  background: linear-gradient(180deg, #2eaf07, rgb(0, 0, 0));

  border-top: 5px solid gray;

  z-index: 2;
}

.GithubReposList {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;

  justify-content: center;
  align-items: center;

  margin: auto;

  list-style-type: none;

  z-index: 4;
}

.GithubRepoListItem {
  display: inline;
  margin: 1rem 1.2rem;
  background-color: wheat;
  border-radius: 35px;

  z-index: 4;

  transition: margin-bottom 0.1s ease, box-shadow 0.1s ease;
}

.GithubRepoListItem:hover {
  box-shadow: 0px 0px 50px rgba(5, 71, 8, 1);
  z-index: 1;
}

.GithubRepo {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 45vw;
  height: 10rem;

  border-radius: 45px;

  background: linear-gradient(
    35deg,
    #000000 0% 0%,
    #083100 20% 20%,
    #0f5501 35% 35%,
    #157a01 60% 60%,
    #1ba100 80% 80%,
    #23cf00 100% 100%
  );
  color: white;
  z-index: 4;

  transition: box-shadow 1s ease;
}

.ct-des-name {
  margin: auto auto;

  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
}
.repoName {
  margin: 0.9rem auto;
}

.repoName a {
  font-size: 1.2rem;
  text-decoration: none;
  color: white;
}
.repoName i {
  font-size: 1.2rem;
  color: rgba(0, 0, 0, 0.74);
}

.repoDescription {
  font-size: 0.8rem;
}
.repoDescription p {
  color: rgb(206, 206, 206);
}
.ct-langs {
  font-size: 0.8rem;

  margin: auto;
}
.langs-list {
  display: flex;
  flex-direction: row;
  list-style-type: none;
}
.langs-list-item {
  color: rgb(165, 157, 140);
  margin: auto 0.5rem;
}

.AboutCard-Container {
  display: flex;
  flex-direction: column;
  justify-content: center;

  margin-top: 3rem;

  width: 100%;
  height: 100vh;
  min-height: 30rem;
  /* height: 39rem; */

  background: linear-gradient(90deg, rgb(0, 0, 0), #500303);
  background-repeat: no-repeat;

  position: relative;
}
.bg-clevelandImage {
  width: 100%;
  height: 100%;
  margin: auto;
  position: absolute;
  background-image: url("https://upload.wikimedia.org/wikipedia/tr/0/09/Cleveland-Cavaliers-Logo.png");
  background-size: 25rem;
  background-repeat: no-repeat;
  background-position: center;
  opacity: 0.03;
  z-index: 3;
  pointer-events: none;
}

.AboutCard-nm {
  margin: auto;
  color: whitesmoke;
  font-size: 2rem;
  box-shadow: black;
}
.AboutMe-Container {
  width: 100%;
  height: 75%;
  min-height: 25rem;
  margin: auto;
  margin-top: -0.3rem;

  text-align: center;

  background: linear-gradient(180deg, #800404, rgb(0, 0, 0));

  border-top: 5px solid #cea02a;

  z-index: 2;
}
.AboutMe-P {
  word-spacing: 4px;
  font-size: 1rem;
  margin: auto;
  padding: 15px 3px;
  color: rgb(214, 211, 211);
  text-shadow: 3px 3px 15px black;

  transition: text-shadow 0.3s;
  z-index: 5;
}
.AboutMe-P:hover {
  text-shadow: 3px 3px 5px black;
}

.schoolInfo {
  display: block;
  width: 80%;
  margin: auto;
  text-align: center;
  margin-top: 8vh;
}
.githubLink {
  cursor: pointer;
  color: #cea02a;
  text-decoration: none;
  -webkit-text-decoration-color: rgb(104, 0, 0);
          text-decoration-color: rgb(104, 0, 0);

  transition: color 0.3s ease;
}
.githubLink:hover {
  color: #bd87009c;
}

.soonDiv {
  position: relative;
}
.soonTag {
  font-size: 0.7rem;
  position: absolute;
  top: -4px;
  right: 0.3rem;
  display: inline;
  color: rgba(209, 209, 209, 0.825);
  background-color: rgba(0, 0, 0, 0.516);
  z-index: 5;
}

.goTo {
  display: flex;
  align-items: center;

  position: fixed;

  width: 4rem;
  height: 4rem;
  bottom: 2rem;

  border-radius: 100%;
  background-color: rgba(255, 255, 255, 0.336);

  text-decoration: none;
  cursor: pointer;

  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;

  overflow: hidden;

  z-index: 5555;
}
.goTo i {
  cursor: pointer;

  margin: auto;
  color: #130101;
  font-size: 2rem;
  border-radius: 100%;
}

.goToFirst {
  right: 7rem;
}

.goToLast {
  left: 7rem;
}

@media screen and (max-width: 720px) {
  .goToFirst {
    right: 4rem !important;
  }
  .goToLast {
    left: 4rem !important;
  }
}

