.goTo {
  display: flex;
  align-items: center;

  position: fixed;

  width: 4rem;
  height: 4rem;
  bottom: 2rem;

  border-radius: 100%;
  background-color: rgba(255, 255, 255, 0.336);

  text-decoration: none;
  cursor: pointer;

  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;

  overflow: hidden;

  z-index: 5555;
}
.goTo i {
  cursor: pointer;

  margin: auto;
  color: #130101;
  font-size: 2rem;
  border-radius: 100%;
}

.goToFirst {
  right: 7rem;
}

.goToLast {
  left: 7rem;
}

@media screen and (max-width: 720px) {
  .goToFirst {
    right: 4rem !important;
  }
  .goToLast {
    left: 4rem !important;
  }
}
