@media screen and (max-width: 800px) {
  .ProfileCart {
    height: calc(80vh);
  }
  .navList {
    padding: 1.2rem 0.2rem !important;
  }
  .navList-item a {
    padding: 0.8rem 1.6rem !important;
    margin: 0 1.2vw !important;
  }
}

.ProfileCart {
  margin: auto;
  display: flex;

  flex-direction: column;
  justify-content: center;
  align-items: center;

  width: 100%;
  height: calc(100vh);

  background: linear-gradient(45deg, rgb(0, 0, 0), rgb(61, 5, 165));
  background-repeat: no-repeat;

  -webkit-animation: navBarColor 10s infinite alternate;

  animation: navBarColor 10s infinite alternate;

  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}

.navImage {
  display: flex;
  justify-content: center;
  align-items: center;

  margin: 5px;
  margin-top: -5rem;
  width: 12rem;
  height: 12rem;

  background-size: 250px;
  background-position: center;

  overflow: hidden;
  border-radius: 100%;
  border: 3px solid rgb(0, 0, 0);

  background-color: gray;
  -webkit-animation: rollAnim 12s infinite;
  animation: rollAnim 12s infinite;
  transition: width 0.3s ease, height 0.3s ease, background-size 0.3s ease,
    border-width 0.3s ease, border-radius 0.3s ease;
}
.navImage:hover {
  background-size: 260px;
}

.navName {
  margin-top: 1rem;
  margin-bottom: 0.8rem;
  font-size: 2rem;

  letter-spacing: 1px;
  text-align: center;
  color: white;
}
.navList {
  list-style-type: none;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: white;
  padding: 1rem 1rem;

  border-radius: 15px;
}

.navList-item a {
  color: black;
  text-decoration: none;
  cursor: pointer;
  letter-spacing: 1px;
  font-size: 0.8rem;
  text-align: center;

  padding: 0.8rem 1.6rem;
  margin: 0 1.8vw;

  background-color: white;
  box-shadow: 0px 0px 12px rgba(61, 5, 165, 0.671);

  border-left: 8px solid rgb(61, 5, 165);
  border-right: 8px solid rgb(61, 5, 165);

  transition: border-radius 0.3s, box-shadow 0.2s ease;
}

.navList-item:nth-child(1) a {
  border-radius: 50px;
  border-top-left-radius: 0px;
  border-bottom-right-radius: 0px;
}

.navList-item:nth-child(2) a {
  border-top-left-radius: 0px;
  border-bottom-right-radius: 0px;

  border-radius: 18px;

  border-color: black;
}

.navList-item:nth-child(3) a {
  border-radius: 50px;
  border-bottom-left-radius: 0px;
  border-top-right-radius: 0px;
}
.navList-item a:hover {
  border-radius: 18px;

  border-color: black;
  box-shadow: 0px 0px 18px black;
}

@-webkit-keyframes rollAnim {
  0% {
    box-shadow: 0px 0px 150px rgb(0, 0, 0);
    border-color: rgb(255, 255, 255);
  }
  50% {
    box-shadow: 0px 0px 15px;
  }
  100% {
    border-color: rgb(255, 255, 255);
    box-shadow: 0px 0px 150px rgb(0, 0, 0);
  }
}

@keyframes rollAnim {
  0% {
    box-shadow: 0px 0px 150px rgb(0, 0, 0);
    border-color: rgb(255, 255, 255);
  }
  50% {
    box-shadow: 0px 0px 15px;
    border-color: black;
  }
  100% {
    border-color: rgb(255, 255, 255);
    box-shadow: 0px 0px 150px rgb(0, 0, 0);
  }
}
